import axios, { AxiosError, AxiosInstance, AxiosRequestConfig } from "axios";

const Axios = axios.create({
  baseURL: `${process.env.REACT_APP_API_URL}/${process.env.REACT_APP_API_VER}`,
  timeout: 15000,
  headers: {
    Authorization: "Bearer " + localStorage.getItem("token"),
  },
});

const OpenAxios = axios.create({
  baseURL: `/static`,
  timeout: 15000,
});

const AddCommonHeaders = (Axios: AxiosInstance) => {
  Axios.defaults.headers.common["x-businessId"] =
    localStorage.getItem("businessId") || "";

  Axios.defaults.headers.common["Authorization"] =
    "Bearer " + localStorage.getItem("token");
};

export const AuthenticatedReq = async (
  url: string,
  method: string,
  config?: AxiosRequestConfig<any>
) => {
  try {
    AddCommonHeaders(Axios);
    const res = await Axios({ url, method: method, ...config });
    return res.data;
  } catch (err: any) {
    const reqError = err as AxiosError;
    return reqError.response?.data ?? { success: false, message: err.message };
  }
};

export const OpenReq = async (
  url: string,
  method: string,
  config?: AxiosRequestConfig<any>
) => {
  try {
    const res = await OpenAxios({ url, method: method, ...config });
    return res.data;
  } catch (err: any) {
    const reqError = err as AxiosError;
    return reqError.response?.data ?? { success: false, message: err.message };
  }
};

export default Axios;
