import { Box } from "@mui/material";
import { Navigate } from "react-router-dom";

import { useGlobal } from "Contexts/App";

import Sidebar from "./BusinessSettings/Sidebar";

const BusinessSettings = () => {
  const { isMobile } = useGlobal();

  return isMobile ? (
    <Box height="100vh">
      <Sidebar />
    </Box>
  ) : (
    <Navigate to="/business/general-settings" replace={true} />
  );
};

export default BusinessSettings;
